.card-container {
    position: relative;
    display: flex;
    /* Display cards under each other */
    flex-direction: column;
    /* Stack cards vertically */
    margin: 20px 10px;
    padding: 10px;
    width: 350px;
    background-color: var(--bg-dark);
    border-radius: 5px;
    max-height: 700px;
    box-shadow: 0 10px 20px 5px rgba(0, 0, 0, 0.2);
}

.task-container {
    overflow: auto;
}

.div-container {
    display: flex;
    /* Display divs next to each other */
    flex-wrap: nowrap;
    /* Allow divs to wrap to next line */
    justify-content: flex-start;
    align-items: flex-start;
}

.addbtn-column {
    background-color: transparent;
    color: var(--dark-gray);
    border: 2px dashed var(--dark-gray);
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    width: 350px !important;
    min-width: 350px !important;
    height: 155px !important;
    min-height: 155px !important;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: 1.3em;
    transition: 0.4s ease-in-out;
    box-shadow: none;
}

.addbtn-column:hover {
    color: var(--light-gray);
    background-color: var(--bg-dark);
}

.name-edit {
    display: flex;
    justify-content: flex-start;
    color: var(--light-gray);
    margin: 0 30px 30px;
    border-bottom: 1px solid var(--light-gray);
    column-gap: 10px;
}

.name-edit input {
    width: 70%;
    color: var(--light-gray);
    font-family: var(--basic-font);
    font-size: 1.5em;
    margin: 5px 20px 5px 0;
    background: transparent;
    border: none;
    outline: none;
}

.name-edit input:focus,
.name-edit input:hover,
.name-edit input:active {
    outline: none;
    border: none;
}

.name-edit input::selection {
    color: var(--dark-gray);
    background: var(--light-gray);
}

.name-edit span {
    margin: 4px 0px 6px 0px;
}

.delete-column-button {
    padding: 5px;
    cursor: pointer;
    color: var(--light-gray);
}

.delete-column-button:hover {
    color: var(--important);
}

.edit-action-button {
    padding: 5px;
    cursor: pointer;
}

#check-button:hover {
    color: var(--confirm);
}

#cancel-button:hover {
    color: var(--important);
}

.ai-button,
.ai-button-on-title-bar {
    color: var(--light-gray);
    padding: 5px;
    cursor: pointer;
}

.ai-button:hover {
    color: var(--magic);
}

.code-button {
    color: var(--light-gray);
    padding: 5px;
    cursor: pointer;
}

.code-button:hover {
    color: var(--code);
}

.craft-button {
    color: var(--light-gray);
    padding: 5px;
    cursor: pointer;
}

.craft-button:hover {
    color: var(--craft);
}

.clipboard-button {
    color: var(--light-gray);
    padding: 5px;
    cursor: pointer;
}

.clipboard-button:hover {
    color: var(--light-blue);
}

.generate-task-button {
    position: absolute;
    top: 0;
    left: 0;
    padding: 5px 10px;
}

.option-p {
    color: aliceblue;
}

.agi-submenu {
    width: max-content;
    position: absolute;
    top: 100px;
    right: 0;
    background-color: var(--dark-gray);
    border: 1px solid var(--dark-gray);
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    margin: 5px;
    padding: 5px;
    z-index: 100;
}

.agi-menu-title {
    color: var(--light-gray);
    margin: 0 20px 20px;
    border-bottom: 1px solid var(--light-gray);
}

.agi-menu {
    list-style-type: none;
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.agi-menu li {
    width: 100%;
    padding: 5px;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
}

.agi-menu li:hover {
    color: var(--off-white);
}

.agi-menu li span {
    color: var(--light-gray);
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.agi-menu>li span:first-child {
    width: 20%;
}

.agi-menu>li span:first-child:hover {
    color: var(--off-white);
}

.agi-menu>li span:nth-child(2) {
    width: 70%;
}

.agi-menu>li span:nth-child(2):hover {
    color: var(--off-white);
}

