.filter-menu-title {
    color: var(--light-gray);
    margin: 0 20px 20px;
    border-bottom: 1px solid var(--light-gray);
}

.filter-submenu {
    width: max-content;
    min-width: 250px;
    /* Slightly increased minimum width */
    position: absolute;
    top: 100px;
    right: 0;
    background-color: var(--dark-gray);
    border: 1px solid var(--dark-gray);
    border-radius: 8px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
    margin: 5px;
    padding: 10px;
    z-index: 100;
}

.filter-menu-title {
    color: var(--light-gray);
}

.filter-menu {
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.filter-category {
    margin-bottom: 15px;
    /* Increased margin between categories */
}

.filter-category p {
    color: var(--light-gray);
    margin: 0 0 8px;
}

.filter-checkbox-container {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.filter-checkbox-item {
    display: flex;
    align-items: center;
}

.filter-checkbox-item input {
    margin-right: 8px;
}

.filter-checkbox-item label {
    color: var(--light-gray);
}

.filter-checkbox-item:hover {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
}

.tag-filter-label {
    min-width: 80px;
    text-align: center;
    border-radius: 5%;
}

.select-all-button {
    background-color: var(--light-gray);
    color: var(--dark-gray);
    border: none;
    border-radius: 5px;
    padding: 8px;
    cursor: pointer;
    font-weight: bold;
    margin-top: -20px;
    margin-bottom: 20px;
}

.select-all-button:hover {
    background-color: var(--attachment-link);
}

@media (max-width: 830px) {
    body {
        font-size: 0.9em;
    }

    .div-container {
        flex-wrap: wrap;
    }
}

@media (max-width: 480px) {
    body {
        font-size: 0.75em;
    }

    .div-container {
        flex-direction: column;
    }
}

@media (max-width: 415px) {
    .content {
        padding: 20px 10px;
    }
}

@media (max-width: 380px) {
    .content {
        padding: 20px 5px;
    }
}

@media (max-width: 370px) {
    .content {
        padding: 20px 0px;
    }
}